
import XHeader from 'vux/src/components/x-header/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import base64 from 'vux/src/tools/base64/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

import { getStore, setStore } from '../../../config/mUtils'
import { doEvaluate, delImg } from '../../../service/getData'
export default {
data() {
return {
title: '发表评论',
max: 5,
show: false,
isdisabled: false,
index: 0,
id: 0,
raterText: '非常好',
textAreaTitle: '对我们的产品还满意吗？说说您的使用心得。',
list: [],
rows: 5,
value: 5,
textAreaVal: '',
service: 5,
logistics: 5,
faction: 5
}
},
components: {
XHeader,
Rater,
Group,
XTextarea,
base64,
Confirm
},
methods: {
del(index, id) {
this.show = true
this.index = index
this.id = id
},
onConfirm() {
delImg(this.id).then(resolved => {
if(resolved.msgCode == '0000') {
this.list.splice(this.index, 1)
} else {
this.$nextTick(() => {
this.$vux.toast.show({
text: resolved.msg
})
})
}
})
},
done() {
doEvaluate(getStore('userId'), getStore('nickname'), getStore('headPic'), this.$route.query.id, this.textAreaVal, this.value, this.service, this.logistics, this.faction).then(resolved => {
if(resolved.msgCode == '0000') {
this.$vux.toast.show({
text: '提交成功'
})
setTimeout(() => {
this.$router.back()
}, 1001)
} else {
this.$vux.toast.show({
text: resolved.msg
})
}
})
},
flie(val) {
this.$vux.loading.show({
text: '上传中...'
})
this.isdisabled = true
let files = document.getElementById('file').files
if(files && files.length) {
let param = new FormData(); //创建form对象
param.append('attachmentId', this.$route.query.id);
param.append('userId', getStore('userId'));
param.append('attachmentType', 3);
param.append('file', files[0]);
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
}; //添加请求头
this.$ajax.post('http://test.celelink.com/one_mall/backend/base/saveImg', param, config).then(response => {
let data = JSON.parse(base64.decode(response.data))
if(data.msgCode === '0000') {
this.list.push(data.data)
this.$nextTick(() => {
this.$vux.loading.hide()
this.$vux.toast.show({
text: data.msg
})
})
} else {
this.$vux.loading.hide()
}
this.isdisabled = false
}).catch((err) => {
this.isdisabled = false
console.log(err)
})
}
}
},
filters: {
rater(val) {
if(val === 1) {
return '非常差'
} else if(val === 2) {
return '差'
} else if(val === 3) {
return '一般'
} else if(val === 4) {
return '好'
} else if(val === 5) {
return '非常好'
}
}
}
}

function getImgURL(node) {
var imgURL = "";
try {
var file = null;
if(node.files && node.files[0]) {
file = node.files[0];
} else if(node.files && node.files.item(0)) {
file = node.files.item(0);
}

try {

imgURL = file.getAsDataURL();

} catch(e) {

imgRUL = window.URL.createObjectURL(file);

}
} catch(e) { //这里不知道怎么处理了，如果是遨游的话会报这个异常

if(node.files && node.files[0]) {
var reader = new FileReader();
reader.onload = function(e) {
imgURL = e.target.result;
};
reader.readAsDataURL(node.files[0]);
}
}
console.log(imgURL)
return imgURL;
}



